    name: "AdminPanelCom",
    data() {
        return {
            AllPanelData: [{
                ID: 0,
                name: "",
                url: "",
                id: "",
                secret: ""
            }],
            AddPanelData: {
                name: "",
                url: "",
                id: "",
                secret: ""
            },
            DelPanelData: {
                uid: 0
