                url: "",
                id: "",
                secret: ""
            }
        }
    },
    methods: {
        // 获取所有变量信息
        GetAllPanelData(){
            axios.get("/v2/api/env/panel/all").then((res) => {
                // 请求成功
                switch (res.data !== "") {
                    case res.data.code === 2000:
                        this.AllPanelData = res.data.data
                        break
                }
            }).catch((error) => {
                // 请求失败
                mdui.snackbar({
                    message: error,
                    position: 'right-top',
                });
            })
        },
        // 打开添加标签
        OpenPanelAdd(){
            let inst = new mdui.Dialog('#panelAdd');
            inst.toggle()
        },
        // 打开删除标签
        OpenPanelDel(panelID){
            this.DelPanelData.uid = panelID
            console.log(panelID)
            console.log(this.DelPanelData.uid)
            let inst = new mdui.Dialog('#panelDel');
            inst.toggle()
        },
        // 打开更新标签
        OpenPanelUpdate(panelID, name, url, id, secret){
            this.UpdatePanelData.uid = panelID
            this.UpdatePanelData.name = name
            this.UpdatePanelData.url = url
            this.UpdatePanelData.id = id
            this.UpdatePanelData.secret = secret
            let inst = new mdui.Dialog('#panelUpdate');
            inst.toggle()
        },
        // // 添加面板
        // AddPanel(){
        //     axios.post("/v2/api/env/panel/add", this.AddPanelData).then((res) => {
        //         // 请求成功
        //         switch (res.data !== "") {
        //             case res.data.code === 2000:
        //                 // 添加成功
        //                 mdui.snackbar({
        //                     message: '面板信息添加成功',
        //                     position: 'right-top',
        //                 });
        //                 setTimeout(() => {
        //                     location.reload()
        //                 }, 1000)
        //                 break
        //             case res.data.code === 5016:
        //                 // 变量名创建失败
        //                 mdui.snackbar({
        //                     message: '面板信息添加失败',
        //                     position: 'right-top',
        //                 });
        //                 break
        //             case res.data.code === 5002:
        //                 // 传递参数错误
        //                 if (res.data.data === "") {
        //                     mdui.snackbar({
        //                         message: '服务繁忙',
        //                         position: 'right-top',
        //                     });
        //                 } else {
        //                     mdui.snackbar({
