        // },
        // // 删除面板
        // DelPanel(){
        //     axios.delete("/v2/api/env/panel/del", {data: this.DelPanelData}).then((res) => {
        //         // 请求成功
        //         switch (res.data !== "") {
        //             case res.data.code === 2000:
        //                 mdui.snackbar({
        //                     message: '面板删除成功',
        //                     position: 'right-top',
        //                 });
        //                 setTimeout(() => {
        //                     location.reload()
        //                 }, 1000)
        //                 break
        //         }
        //     }).catch((error) => {
        //         // 请求失败
        //         mdui.snackbar({
        //             message: error,
        //             position: 'right-top',
        //         });
        //     })
        // },
        // // 更新面板
        // UpdatePanel(){
        //     axios.put("/v2/api/env/panel/update", this.UpdatePanelData).then((res) => {
        //         // 请求成功
        //         switch (res.data !== "") {
        //             case res.data.code === 2000:
        //                 mdui.snackbar({
        //                     message: '面板修改成功',
