        // 检查面板连通性
        CheckPanelSuccess(url, id, secret){
            axios.post("/v2/api/panel/connect", {url: url, id: id, secret: secret}).then((res) => {
                // 请求成功
                switch (res.data !== "") {
                    case res.data.code === 2000:
                        // 连通测试成功
                        mdui.snackbar({
                            message: '连接测试成功',
                            position: 'right-top',
                        });
                        break
                    case res.data.code === 5014:
                        // client_id或client_secret有误
                        mdui.snackbar({
                            message: res.data.msg,
                            position: 'right-top',
                        });
                        break
                    case res.data.code === 5003:
                        // 服务错误
                        mdui.snackbar({
                            message: '服务繁忙, 可能是URL地址错误',
                            position: 'right-top',
                        });
                        break
                    case res.data.code === 5002:
                        // 传递参数错误
                        if (res.data.data === "") {
                            mdui.snackbar({
                                message: '服务繁忙',
                                position: 'right-top',
                            });
                        } else {
                            mdui.snackbar({
                                message: res.data.msg,
                                position: 'right-top',
                            });
                        }
                        break
                }
